<template>
    <div class="demo-3">
        <div class="content content--intro">
            <!-- 标题 -->
            <div class="content__inner">
                <h2 class="content__title">徐航</h2>
                <p class="content__subtitle">WeChat</p>
                <viewer>
                    <img src="../assets/weixin.png" style="width:100px;height:100px;cursor: pointer;">
                </viewer>
                <router-link to="#" class="enter" style="z-index:5">enter</router-link>
            </div>
            <!-- 过渡动画 -->
            <div class="shape-wrap">
                <svg class="shape" width="100%" height="100vh" preserveAspectRatio="none" viewBox="0 0 1440 800" xmlns:pathdata="http://www.codrops.com/">
                    <path d="M 73.3,178.6 C 101.7,363.8 76.38,735 118.7,813.8 161,892.7 327.3,946.7 381.1,853.3 434.9,759.9 427.2,488.9 436.8,341.5 443.3,241.3 447.3,33.05 516.1,36.19 574.9,38.88 611.6,214.9 622.3,429.7 633,644.6 614.7,796.1 688.1,849 761.6,901.8 860.7,873.7 897.6,850 982.3,795.5 951.2,639.3 961.1,506.1 970.9,372.9 958.5,43.53 1023,43.47 1063,43.43 1085,173.6 1095,370.7 1105,567.8 1082,804.3 1165,842.6 1197,857.5 1304,901 1342,833 1380,765 1354,413.7 1379,156.2 1407,-137.5 1719,-12.96 1719,-12.96 L -53.5,-44.66 C -53.5,-44.66 44.91,-6.65 73.3,178.6 Z" pathdata:id="M 105.3,190.6 C 159.7,353.8 143.2,774.2 149.1,779.5 155,784.8 159.4,782 164.8,778.2 170.2,774.4 168.9,242.8 240.3,125 311.7,7.205 430.7,2.307 564.2,13.56 707.9,25.67 806,166.3 800.5,376 804.7,587.3 801.2,773.9 807.1,782.7 813,791.4 816.8,792.7 821.4,786 826.4,778.8 819.4,566.3 820.3,498.1 821.2,429.9 781.4,95.51 992.5,74.58 1108,63.14 1235,166.4 1250,359.4 1265,552.4 1248,763.7 1271,781.4 1277,786 1281,786.2 1286,779.7 1292,773.2 1260,251.3 1355,103.9 1441,-30.35 1610,-117.6 1610,-117.6 L -110.1,-132.3 C -110.1,-132.3 50.91,27.35 105.3,190.6 Z"></path>
                </svg>
            </div>
        </div>
    </div>
</template>
<script>
import anime from 'animejs'
import charming from 'charming'
export default {
    mounted(){
            const DOM = {};
            DOM.intro = document.querySelector('.content--intro');
            DOM.shape = DOM.intro.querySelector('svg.shape');
            DOM.path = DOM.shape.querySelector('path');
            DOM.enter = document.querySelector('.enter');
            // Set the SVG transform origin.
            DOM.shape.style.transformOrigin = '50% 0%';

            // 注册事件
            const init = () => {
                DOM.enter.addEventListener('click', navigate);
                DOM.enter.addEventListener('touchenter', navigate);
            };

            let loaded;
            // 点击事件函数 触发动画
            const navigate = () => {
                if ( loaded ) return;
                loaded = true;

                anime({
                    targets: DOM.intro,
                    duration: 1600,
                    easing: 'easeInOutCubic',
                    translateY: '-200vh'
                });

                anime({
                    targets: DOM.shape,
                    easing: 'easeInOutCubic',
                    scaleY: [
                        {value: [0,1], duration: 800},
                        {value: 0, duration: 1200, easing: 'easeOutElastic', elasticity: 700}
                    ]
                });

                anime({
                    targets: DOM.path,
                    duration: 800,
                    easing: 'easeInOutQuad',
                    d: DOM.path.getAttribute('pathdata:id')
                });

                anime({
                    targets: [DOM.intro.querySelector('.content__inner'), DOM.shape],
                    duration: 1300,
                    easing: 'linear',
                    backgroundColor: (t,i) => {
                        console.log('here')
                        if ( i === 0 ) return '#fff1fb';
                    },
                    fill: (t,i) => {
                        if ( i === 1 ) return '#fff1fb';
                    }
                });
            };
            init();
    }
}
</script>
<style scoped>

/* Color schemes */

.demo-3 {
	--color-text: #fff;
	--color-link: #4c0a75;
	--color-link-hover: #f470d2;
	--color-enter: #f470d2;
    /* 大标题颜色 */
	--color-title: #797878;
    /* 小标题颜色 */
	--color-subtitle: #b45aeba4;
	--color-menu-item: #000;
	--color-menu-item-hover: #4c0a75;
	--color-menu-item-hover-alt: #8544da;
    /* 背景颜色 */
	--color-content: #c0bebd81;
	--color-content-fixed: #ddd;
	--font-alt: 400 6vw/1 'Chewy', serif;
	--color-next: #fff;
	--color-next-hover: #000;
}


/* Page transitions */
main {
	height: 100vh;
	width: 100%;
	overflow: hidden;
	position: relative;
}

.content {
	width: 120%;
	height: 100%;
	position: relative;
}
.content__inner {
	width: 100%;
	height: 100vh;
	position: relative;
	text-align: center;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.content--intro .content__inner {
	background: var(--color-content);
}

.content h2 {
	margin: 0;
}

.shape-wrap {
	position: relative;
	z-index: 10;
	margin: -5px 0 0 0; /* Hide the gap */
}

.shape {
	height: 100vh;
	width: 100%;
	display: block;
	fill: var(--color-content);
}

.content__title {
	font: var(--font-alt);
	color: var(--color-title);
	margin: 0;
	line-height: 1;
}
.content__subtitle {
	font: var(--font-alt);
	font-size: 1.5em;
    font-weight: 600;
	color: var(--color-subtitle);
	padding: 0.5em 0 0 0;
	margin: 0;
}

.enter {    
	margin: 6vh 0 0 0;
	font-size: 1em;
	letter-spacing: 3px;
	cursor: pointer;
	font-weight: bold;
	white-space: pre;
	color: var(--color-enter);
	pointer-events: auto;
}
</style>



