<template>
    <div class="main">
        <div class="samrt-scale item">
            <h2 title="也没多智能">智能料理秤</h2>
            <span>演示视频 ↓</span><br>
            <img src="../assets/smart.png" class="samrt-scale-vedio" title="演示视频" @click="toVideo">
            <p>-----------------------------</p>
            <p style="text-align:left">
                <strong>说明:</strong> 这是一个带手机app的电子秤，电子秤的数据通过蓝牙传送到手机，手机app接收并处理<br><br>
                这是上学期间的一个课程设计，也算是和程序开发沾边吧。<br>
                一个星期学的java和安卓，做了这个app
            </p>
        </div>
        <div class="graduation-design item">
            <h2>胶囊表面缺陷检测系统</h2>
            <span>源代码 ↓</span><br>
            <img src="../assets/source.png" class="graduation-design-vedio" title="源代码地址" @click="toVideo">
            <p>-----------------------------</p>
            <p style="text-align:left">
                <strong>说明:</strong> TensorFlow + OpenCV + Python + 摄像头 ----→ 组成的检测胶囊表面损坏之类的系统<br><br>
                花了半个月学了点神经网络基础， 
                python边用边学， 
                花了一个星期学了opencv，
                研究了几个基础神经网络模型<br>
                <del>数学太差只能研究到这个地步了</del>
            </p>
        </div>
        <div class="ife item">
            <h2>两个月的前端之旅</h2>
            <!-- <p>感谢百度前端学院的老师们</p> -->
            <p id="ife" @click="toVideo">→ 每个阶段的任务代码 ←</p>
            <p>-----------------------------</p>
            <h3>可视化数据表</h3>
            <p>node.js + mongoDB做的</p>
            <p id="visualization" style="text-decoration: underline" @click="toVideo">代码地址</p>
            <p>因为需要本地服务所以演示为gif图演示↓</p>
            <viewer>
                <img src="../assets/可视化数据表.gif" style="width:300px;height:200px;cursor: pointer;">
            </viewer>
            <h3>餐厅小游戏</h3>
            <p>观察者模式 多个对象间传递消息</p>
            <p id="restaurant" style="text-decoration: underline" @click="toVideo">代码地址</p>
            <p id="restaurant-demo" style="text-decoration: underline" @click="toVideo">demo地址</p>
        </div>
    </div>
</template>
<script>
export default {
    methods:{
        toVideo(e) {
            if (e.target.className === 'samrt-scale-vedio') {
                window.open('https://weibo.com/tv/v/EDBnIDWqP?fid=1034:e08326bbaffb868bb2130e531dc86ada')
            }
            else if(e.target.className === 'graduation-design-vedio') {
                window.open('https://github.com/TSjianjiao/Defect-Detection-with-tensorflow')
            }
            else if(e.target.id === 'ife') {
                window.open('https://github.com/TSjianjiao/IFE')
            }
            else if(e.target.id ==="visualization") {
                window.open('https://github.com/TSjianjiao/IFE/tree/master/2018IFE/%E7%AC%AC39-41%E5%A4%A9')
            }
            else if(e.target.id === 'restaurant') {
                window.open('https://github.com/TSjianjiao/IFE/tree/master/2018IFE/%E7%AC%AC50-53%E5%A4%A9')
            }
            else if(e.target.id === 'restaurant-demo') {
                window.open('https://tsjianjiao.github.io/index.html')
            }
        }
    }
}
</script>

<style scoped>
a {
    text-decoration: none;
}
.main {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    height: inherit;
    background-color: #6bffcd41;
}
.item {
    width: 300px;
    height: 95%;
    text-align: center;
    border: solid 1px #fff;
    box-shadow: 3px 1px 4px  rgb(121, 120, 120);
    background: rgba(255, 255, 255, 0.562);
    overflow: auto;
}
.samrt-scale-vedio,
.graduation-design-vedio {
    width: 70px;
    height: 60px;

}
.samrt-scale-vedio:hover,
.graduation-design-vedio:hover {
    cursor: pointer;
}
</style>




