<template>
  <div>
    <full-page :options="options" id="fullpage">
      <div class="section">
        <progressive-img :src="image_1"
        :placeholder="image_1"
        blur:50/>
      </div>
      <div class="section">
        <progressive-img :src="image_1"
        :placeholder="image_1"
        blur:50/>
      </div>
    </full-page> 
  </div>
</template>
<script>
import $ from 'jquery'
import fullpage from '@/js/jquery.fullPage'
export default {
    data() {
      return {
        options: {
          paddingTop: '0px',
        },
      }
    },
    mounted() {
        // 滚动速度
        $.fn.fullpage.setScrollingSpeed(700);
    },
    // 销毁fullpage初始化
    beforeDestroy() {
        $.fn.fullpage.destroy('all');
    },
    computed: {
        image_1 () {
        return require('../assets/bg3.png')
        }
    }
  }
</script>
<style>
#fullpage {
    background-color: #aad23d6e;
}
</style>



