<template>
    <div class="main">
        <!-- 左侧导航 -->
        <v-select v-if="isShow" key="a"></v-select> 
         <!-- 内容 -->
        <transition name="fade" mode="out-in" >
            <router-view></router-view>
        </transition>
    </div>
</template>

<script>
export default {
    data(){
        return{
            isShow: false,
            timer1: null
        }
    },
    watch:{
        '$route'(to, from) {
            // 只在封面进入时触发
            if(to.path === '/') {
                // 延时等待进入动画完成
                clearTimeout(this.timer1);
                this.timer1 = setTimeout(()=>{
                    this.isShow = true;
                    // 程序触发导航
                    this.$router.push('/home')
                }, 1000);
            }
        }
    },
}
</script>

<style scoped>
.main{
    display: flex;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s; 
}
.fade-enter, .fade-leave-to  {
  opacity: 0;
}
</style>



