<template>
    <div class="select-ama">
        <section class="content">
            <nav id="wrapper" ref="wrapper" class="menu menu--ama">
                <router-link class="menu__item" to="/home">
                    <span class="menu__item-name">关于我</span>
                </router-link>
                <router-link class="menu__item" to="/works">
                    <span class="menu__item-name">作品</span>
                </router-link>
                <router-link class="menu__item" to="/my">
                    <span class="menu__item-name">联系我</span>
                </router-link>
                <router-link class="menu__item" to="/thx">
                    <span class="menu__item-name">更多</span>
                </router-link>
                <a href=""></a>
                <a href=""></a>
            </nav>
        </section>
    </div> 
</template>
<script>
import anime from 'animejs'
import charming from 'charming'
export default {
    name:'my-select',
    mounted() {
        const items = Array.from(document.querySelectorAll('.menu > .menu__item'));
        class Item {
        constructor(el) {
            this.DOM = {};
            this.DOM.el = el;
            //保存元素
            this.DOM.name = el.querySelector('.menu__item-name');
            this.colors = {
                // 获取css设置的颜色 点击之后的
                active: window.getComputedStyle(this.DOM.el).getPropertyValue('--menu-item-color')
            };
            //charming插件 把el里面的每一个字符变为el的子节点 
            charming(this.DOM.name);
            // 每个字符都是一个span标签 转化为数组
            this.DOM.nameLetters = [...this.DOM.name.querySelectorAll('span')];
            // 获取原始颜色
            this.colors.initial = window.getComputedStyle(this.DOM.nameLetters[0]).color;
            //启动监听事件
            this.initEvents();
        }
        //事件方法
        initEvents() {
            const duration = 150; // 周期

            //鼠标移入
            this.mouseenterFn = () => this.mouseTimeout = setTimeout(() => {
                this.isActive = true;
                anime.remove(this.DOM.nameLetters);
                // anime动画
                anime({
                    targets: this.DOM.nameLetters,
                    delay: (t,i) => i*20,
                    translateY: [
                        {value: (t,i) => i%2===0?10:-10, duration: duration, easing: 'easeInSine'},
                        {value: (t,i) => i%2===0?[-10,0]:[10,0], duration: duration+700, easing: 'easeOutElastic', elasticity: 600}
                    ],
                    opacity: [
                        {value: 0, duration: duration, easing: 'linear'},
                        {value: 1, duration: duration, easing: 'linear'}
                    ],
                    color: {
                        value: this.colors.active, 
                        duration: 1,
                        delay:(t,i) => i*20+duration, 
                        easing: 'linear'
                    }
                });
            }, 50);
            //鼠标移出
            this.mouseleaveFn = () => {
                clearTimeout(this.mouseTimeout);
                if( !this.isActive ) return;
                this.isActive = false;

                anime.remove(this.DOM.nameLetters);
                anime({
                    targets: this.DOM.nameLetters,
                    delay: (t,i,l) => (l-i-1)*20,
                    translateY: [
                        {value: (t,i) => i%2===0?-10:10, duration: duration, easing: 'easeInSine'},
                        {value: (t,i) => i%2===0?[10,0]:[-10,0], duration: duration+700, easing: 'easeOutElastic', elasticity: 600}
                    ],
                    opacity: [
                        {value: 0, duration: duration, easing: 'linear'},
                        {value: 1, duration: duration, easing: 'linear'}
                    ],
                    color: {
                        value: this.colors.initial, 
                        duration: 1,
                        delay:(t,i,l) => (l-i-1)*20+duration, 
                        easing: 'linear'
                    }
                });
            };

            this.DOM.el.addEventListener('mouseenter', this.mouseenterFn);
            this.DOM.el.addEventListener('touchstart', this.mouseenterFn);
            this.DOM.el.addEventListener('mouseleave', this.mouseleaveFn);
            this.DOM.el.addEventListener('touchend', this.mouseleaveFn);
        }
        };
        items.forEach(item => new Item(item));
    }
}
</script>
<style scoped>
.menu--ama {
    /* 计数器 */
	counter-reset: itemCounter;
}

.menu--ama .menu__item {
	font-family: 'Quicksand', sans-serif;
	font-size: 1.25em;
	font-weight: 500;
	margin: 3em 0;
	padding-left: 0.5em;
	color: #32343c;
}
/*点击之后的颜色*/
/*有几项设置几个*/
.menu--ama .menu__item:first-child {
    /* 颜色变量 */
	--menu-item-color:#9437ff;
}

.menu--ama .menu__item:nth-child(2) {
	--menu-item-color:#ff84fd;
}

.menu--ama .menu__item:nth-child(3) {
	--menu-item-color:#6cffce;
}

.menu--ama .menu__item:nth-child(4) {
	--menu-item-color:#aad23d;
}
/* 添加序号 */
.menu--ama .menu__item::before {
	content: counter(itemCounter,decimal-leading-zero);
	font-size: 0.85em;
	font-weight: bold;
	position: absolute;
	right: 100%;
	bottom: calc(100% - 0.35em);
	counter-increment: itemCounter;
	color: #9c9a9a;
}
/* 鼠标进入 */
.menu--ama .menu__item:hover::before,
.menu--ama .menu__item:focus::before {
	color: var(--menu-item-color);
}

.menu--ama .menu__item-name {
	font-size: 1.5em;
	font-weight: 500;
	display: flex;
	flex-wrap: wrap;
	padding: 0.5em 0 0 0;
	white-space: pre;
	text-transform: lowercase;
}
/* 进度条默认 */
.menu--ama .menu__item-name::before,
.menu--ama .menu__item-name::after {
	content: '';
	position: absolute;
	bottom: 100%;
	left: 0;
	width: 2.5em;
	height: 4px;
	background: #dbdada;
}

.menu--ama .menu__item-name::after {
	background: var(--menu-item-color);
	transform: scale3d(0,1,1);
	transform-origin: 0% 50%;
	transition: transform 0.5s;
}
/* 进度条变换颜色 */
.menu--ama .menu__item:hover .menu__item-name::after,
.menu--ama .menu__item:focus .menu__item-name::after,
.menu--ama .router-link-active .menu__item-name::after {
	transform: scale3d(1,1,1);
}

.menu--ama .menu__item-name span {
	display: inline-block;
}


*,
*::after,
*::before {
	box-sizing: border-box;
}
/* Color schemes */
.select-ama {
	--color-text: #9a9da7;
	--color-background: #fff;
	--color-link: #32343c;
	--color-link-hover: #9a9da7;
}


/* Fade effect */
a {
	text-decoration: none;
	color: #5d93d8;
	color: var(--color-link);
	outline: none;
}

a:hover,
a:focus {
	color: #423c2b;
	color: var(--color-link-hover);
	outline: none;
}

/* 选择项位置 */
.content {
	position: relative;
    left: 0;
	display: flex;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
    width: 200px;
	padding: 3em 2em;
    background-color: #ececec;
}



/* Common styles for the menus */
.menu {
	position: relative;
	z-index: 10;
}

.menu__item {
	line-height: 1;
	position: relative;
	display: block;
	margin: 1em 0;
	outline: none;
}

.menu__item-name,
.menu__item-label {
	position: relative;
	display: inline-block;
}

.menu__item-name {
	font-size: 1.25em;
}

.menu__item-label {
	margin: 0 0 0 0.5em;
}


</style>



